*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}
.card{
  width: 100%;
  height: auto;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgb(0,0,0,0.5);
  border-radius: 15px!important;
}
h1{
  color: white;
  margin-bottom: 1rem;
  font-size: 4rem;
  min-width:4rem;
  box-sizing: border-box;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.02857em;
  text-shadow: 5px 5px 5px rgba(0,0,0,0.3);
  text-align: center;
  background-color:  #8566aa;
}
input{
  text-align: center;
  height: 2rem;
  top: 1rem;
  border: none;
  background: transparent;
  font-size: 1.5rem!important;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
  background-color: white;
}
.but{
  height: 3rem!important;
  width: 3rem!important;
  border-radius: 50%!important;
  background-color:  #8566aa;
  color: #fff;
  font-size: 1.5rem!important;
  border: none;
  outline: none!important;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
  
}
.but:hover{
  background-color: #20bf6b;
}
ul{
  margin-top: 30px;
  background-color: white;
}
ul li{
  padding-left: 0px;
  text-align: left;
  font-size: 1.5rem!important;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
  background-color: white;
  /* text-shadow: 5px 5px 5px rgba(0,0,0,0.3); */

}

@media(max-width: 768px){
  .center_div{
    width: 55%;
  }
  .but{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;
  }
}
.add{
  height: 3rem!important;
  width: 3rem!important;
  background-color:  #8566aa;
  color: #fff;
  font-size: large;
  border-radius: 50%;
}
.add:hover{
  background-color: #20bf6b;
}
.delete{
  height: 2rem!important;
  width: 2rem!important;
  background-color:  #8566aa;
  color: #fff;
  font-size: large;
  border-radius: 50%;
}
.delete:hover{
  background-color: red;
}
.main_page{
  display: flex;
  justify-content: center;
  align-items: center;
}